@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,600,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.imgContainer {
    background-color: #F3F3F3;
}

.search-lot--content {
    background-color: #FFFFFF;
    font-family: 'Dosis', sans-serif;
    height: 100%;
}

.lot-container > div {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.lot-container > div > div:last-child {
    height: 100%;
}

.title {
    font-size: 1.625rem;
    line-height: 1.2;
    letter-spacing: 1px;
    /*font-size: 26px;*/
    /*line-height: 28px;*/
}

.subtitle {
    color: #968a8a;
    font-size: 1.25rem;
    line-height: 1.2;
    letter-spacing: 1px;
    /*font-size: 20px;*/
    /*line-height: 28px;*/
}

.emptyPhoto {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 250px;
}

@media screen and (max-width: 576px) {
    .emptyPhoto {
        height: auto;
    }
}

.favourite {
    padding: 1rem;
}
 
.favourite-content h3 {
    font-size: 1rem;
}
.h-site-search--search {
    /*padding: 2.875rem 2.5rem;*/
    -webkit-transition: 0.7s all cubic-bezier(.73,.05,.69,.94);
    transition: 0.7s all cubic-bezier(.73,.05,.69,.94);
    /*background-color: white;*/
    z-index: 1000;
    width: 100%;
}

@media screen and (max-width: 567px) {
    .h-site-search--search .container {
        padding: 0;
    }
}

.h-site-search--form .h-form-control::-webkit-input-placeholder {
    color: #5555;
}

.h-site-search--form .h-form-control:-ms-input-placeholder {
    color: #5555;
}

.h-site-search--form .h-form-control::-ms-input-placeholder {
    color: #5555;
}

.h-site-search--form .h-form-control::placeholder {
    color: #5555;
}

.h-site-search--searchbox {
    -webkit-transition: -webkit-transform .3s;
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}

.h-site-search--form .h-form-control {
    margin-left: 0;
}

.ui.icon.input > input {
    padding-right: 2.67142857em !important;
}

.ui.input {
    font-size: 1em;
    width: 100%;
}

.ui.input {
    position: relative;
    font-weight: normal;
    font-style: normal;
    display: inline-flex;
    color: rgba(0, 0, 0, 0.87);
}

.ui.input > input {
    margin: 0em;
    max-width: 90%;
    flex: 1 0 auto;
    outline: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    text-align: left;
    line-height: 1.21428571em;
    font-family: 'Lato', 'Helvetica Neue', Arial, Helvetica, sans-serif;
    padding: 0.67857143em 1em;
    background: #FFFFFF;
    border: 0 solid rgba(34, 36, 38, 0.15); /* 1px work */
    color: rgba(0, 0, 0, 0.87);
    border-radius: 0;
    /*border-radius: 0.28571429rem;*/
    -webkit-transition: border-color 0.1s ease, -webkit-box-shadow 0.1s ease;
    -webkit-transition: box-shadow 0.1s ease, border-color 0.1s ease;
    transition: box-shadow 0.1s ease, border-color 0.1s ease;
    box-shadow: none;
}

