.h-site-search--search-empty {
    padding: 8rem 2.5rem 12rem;
    z-index: 1000;
}

.searchBox .h-form-item {
    flex: 100% 0 1;
}

.searchBox .vertical-center.move {
    -webkit-transform: translate(0);
            transform: translate(0);
}

.h-site-search--search-wrap{
    width: 100%;
    z-index: 1000;
}

.input-group.mb-3{
    display: block;
}

.h-search-filter .ui.button.primary .svg-inline--fa.fa-w-16{
    color: #ffffff;
}

.h-header.w-100.has-no-hero{
    border-bottom: unset;
}

.tabs {
    border-bottom: 2px solid black;
}

button {
    font-size: 16px;
    line-height: 20px;
    color: #000000;
    background-color: #FFFFFF;
    outline: none;
    border: none;
}

@media screen and (max-width: 768px) {
    button {
        font-size: 14px;
    }
    .h-site-search--search {
        padding: 2rem 0;
    }
}

button.active {
    color: #FFFFFF;
    background-color: #000000;
}

@media (min-width: 992px){
    body .hero-empty {
        min-height: 4.5625rem;
    }
}

.dropdown-item-alt{
    display: block;
    width: 100%;
    padding: 0.25rem 1.5rem;
    clear: both;
    font-weight: 400;
    color: #212529;
    text-align: inherit;
    white-space: nowrap;
    background-color: transparent;
    border: 0;
}

.search-filter button {
    background-color: #000000;
    color: #FFFFFF;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 1px;
}
.search-filter button[type="submit"] {
    background-color: #000000;
    color: #FFFFFF;
    font-size: 16px;
}

input {
    border-radius: 0;
    outline: none;
    border: none;

    color: #000000;
    font-weight: bold;
}
input::-webkit-input-placeholder {
    color: #000000;
    font-weight: bold;
}
input:-ms-input-placeholder {
    color: #000000;
    font-weight: bold;
}
input::-ms-input-placeholder {
    color: #000000;
    font-weight: bold;
}
input::placeholder {
    color: #000000;
    font-weight: bold;
}

select {
    height: 46px;
    border: none;
    outline: none;
    border-bottom: 2px solid black;
    padding-bottom: 0;
    background-color: transparent;
    max-width: 150px;
    width: 100%;
}

.filter-container {
    border-bottom: 2px solid black;
}

.searchBox .h-form-group label {
    width: auto;
}
body {
    margin: 0;
    font-family: 'Nunito Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #efefef;
  }

.row-spacing {
    margin-bottom: 3rem;
}

body #searchBox .dropdown-item-alt .ui.checkbox{
    display: block;
}

body #searchBox .dropdown-menu[aria-labelledby="Categories"] .dropdown-item-alt .ui.checkbox{
    min-height: 24px;
}

body #searchBox .dropdown-item .ui.checkbox label:before{
    border: 1px solid #D4D4D5;
}

.h-site-search--form .h-form-control {
    border: 1px solid rgba(34, 36, 38, 0.15);
}

a.btn.btn-sm{
    text-transform: unset;
}

.h-search-filter input[type="checkbox"]#upcoming-only + label{
    padding-left: 4.5rem;
}

.error-message{
    font-size: 40px;
    margin-left: 0.875rem;
    text-align: center;
    padding: 40px 0 60px;
}

.preloader-blur{
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(243, 243, 243, 0.4);
    width: 100%;
    height: 100%;
    z-index: 60;
}

body div[aria-busy=true]{
    position: fixed;
    z-index: 100;
}

.widget-fluid .widget-fluid--image img, .imgContainer img{
    object-fit: cover;
    object-position: center;
}






@media (min-width: 1200px) {

    .App .widget-fluid--body {
        width: 50%;
    }

}
